@use 'sass:math';
@use 'sass:string';

@function multiple-box-shadow($n) {
  $value: '#{math.random(2000)}px #{math.random(2000)}px #FFF';

  @for $i from 2 through $n {
    $value: '#{$value}, #{math.random(2000)}px #{math.random(2000)}px #FFF';
  }

  @return string.unquote($value);
}


$shadows-small: multiple-box-shadow(700);
$shadows-medium: multiple-box-shadow(200);
$shadows-big: multiple-box-shadow(100);

.blocksync-stars-area {
    height: 600px;
    overflow: hidden;
    width: 300px;
    @media #{$smlg-device} {
        width: 100% !important;
        min-width: 150px;
    }
}

.blocksync-stars {
  width: .5px;
  height: .5px;
  background: transparent;
  box-shadow: $shadows-small;
  animation: animStar 50s linear infinite;

  &:after {
    content: " ";
    position: absolute;
    top: 2000px;
    width: .5px;
    height: .5px;
    background: transparent;
    box-shadow: $shadows-small;
  }
}

.blocksync-stars2 {
  width: 1px;
  height: 1px;
  background: transparent;
  box-shadow: $shadows-medium;
  animation: animStar 100s linear infinite;

  &:after {
    content: " ";
    position: absolute;
    top: 2000px;
    width: 1px;
    height: 1px;
    background: transparent;
    box-shadow: $shadows-medium;
  }
}
.blocksync-stars3 {
  width: 3px;
  height: 3px;
  background: transparent;
  box-shadow: $shadows-big;
  animation: animStar 150s linear infinite;
  border-radius: 50%;
  &:after {
    content: " ";
    position: absolute;
    top: 2000px;
    width: 3px;
    height: 3px;
     border-radius: 50%;
    background: transparent;
    box-shadow: $shadows-big;
  }
}

@keyframes animStar {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-2000px);
  }
}

// banner two
.landing-page-wrapper{
  position: relative;
    .slider-bg-light{
      position: absolute;
      right: 0%;
      top: 0;
      min-width: 300px;
      z-index: -1;
      @media #{$sm-layout} {
          right: 10%;
          min-width: none;
      }
      @media #{$small-mobile} {
          right: 0%;
      }
      @media screen and (min-width: 2500px) {
          right: 5%;
          transform: translateX(-50%);
      }
    }
    .slider-bg-dot-shape {
        position: absolute;
        right: 1%;
        top: 50px;
        z-index: -1;
        .blocksync-stars-area {
            height: 980px;
            overflow: hidden;
            width: 300px;
        }
    }
    .slider-bg-dot-shape.two-splash {
        position: absolute;
        left: 1%;
        right: auto;
        top: 50px;
        z-index: -1;
        .blocksync-stars-area {
            height: 980px;
            overflow: hidden;
            width: 300px;
        }
    }
}

