/*==============================
 *  Utilities
=================================*/

.container,
.container-fluid,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    @media #{$sm-layout} {
        padding-right: 15px;
        padding-left: 15px;
    }
}

iframe {
    width: 100%;
}

.clearfix:before,
.clearfix:after {
    content: " ";
    display: table;
}

.clearfix:after {
    clear: both;
}

.fix {
    overflow: hidden;
}



/*===============================
    Background Color 
=================================*/

@include config-bg-colors('bg-color-',
    'primary'var(--color-primary),
    'primary-alt'var(--color-primary-alt),
    'secondary'var(--color-secondary),
    'secondary-alt'var(--color-secondary-alt),
    'tertiary'var(--color-tertiary),
    'tertiary-alt'var(--color-tertiary-alt),
    'yellow'var(--color-yellow),
    'yellow-alt'var(--color-yellow-alt),
    'primary-opacity-1'var(--color-primary-opacity-1),
    'primary-opacity-2'var(--color-primary-opacity-2),
    'tertiary-opacity-1'var(--color-tertiary-opacity-1),
    'tertiary-opacity-2'var(--color-tertiary-opacity-2),
    'yellow-opacity-1'var(--color-yellow-opacity-1),
    'yellow-opacity-2'var(--color-yellow-opacity-2),
    'dark'var(--color-dark),
    'dark-alt'var(--color-dark-alt),
    'darker'linear-gradient( 162deg ,#1b1b26 -70% ,#09090d 92% ),
    'heading'var(--color-heading),
    'body'var(--color-body),
    'white'var(--color-white),
    'pink'var(--color-pink),
    'bg-alt'var(--color-bg-alt),

